.about-page {
    text-align: center;
    font-family: Bruno Ace SC, sans-serif;
}


.about-page h1 {
    font-size: 1.5rem;
    margin: 40px 0;
}

.about-page h2 {
    display: inline-block;
    padding: 10px 20px;
    background-color: black;
    color: white;
    font-size: 1rem;
    border: none;
    border-radius: 5px;
    margin: 30px 0;
}

.about-page p {
    font-size: 1rem;
    margin: 30px 0;
    padding: 0 20px;
    line-height: 1.5;
}

.aboutPicture,
section figure img {
    width: 100%;
    height: 400px;
    object-fit: cover;
    display: block;
}

.aboutFigure {
    margin: 0;
    padding-top: 2px;
    position: relative;
}

.aboutPicture {
    width: 100%;
    height: auto;
    object-fit: contain;
    display: block;
}

.aboutCaption {
    background-color: rgba(0, 0, 0, 0.3);
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10px;
    box-sizing: border-box;
    text-align: center;
    color: white;
    text-shadow: 2px 2px 2px black;
}

.aboutCaptionTop {
    font-size: 1.2rem;
}

.aboutCaptionBottom {
    font-size: 1rem;
}

.about-page ul {
    list-style-type: none;
    padding-left: 0;
}

.about-page ul li {
    display: flex;
    align-items: center;
    margin: 25px 0;
}

.about-page ul li i {
    padding-left: 20px;
    margin-right: 10px;
    font-size: 18px;
    color: green;
}

.about-page ul li span {
    text-align: left;
}

.contact-info {
    font-size: 1.2rem;
    margin: 20px;
}

.contact-info p {
    display: inline-flex;
    align-items: center;
    margin-bottom: 10px;
    padding: 0;
}

.contact-info i {
    margin-right: 8px;
}

.contact-info a {
    font-size: 14px;
    color: #333;
    text-decoration: none;
    font-weight: bold;
}

.fa-whatsapp {
    color: #25D366;
    font-size: 1.5rem;
    font-weight: bold;
}

.fa-phone,
.fa-envelope {
    color: #000;
    font-size: 1.5rem;
}